<template>
  <div class="home-view-container">
    <h1>Adopt a new best friend.</h1>
    <b-row>
      <b-col>
        <b-list-group class="my-2 w-50 mx-auto">
          <b-list-group-item>Cats: {{ getAllCats.length }}</b-list-group-item>
          <b-list-group-item>Dogs: {{ getAllDogs.length }}</b-list-group-item>
          <b-list-group-item>Total: {{ animalsCount }}</b-list-group-item>
        </b-list-group>
      </b-col>
    </b-row>

    <b-button variant="primary" class="mb-2" @click="togglePetForm">Add New Pet</b-button>
    <b-form @submit.prevent="handleSubmit" v-if="showPetForm">

      <b-form-group id="input-group-2" label="Pet's Name:" label-for="input-2">
        <b-form-input
          id="input-2"
          v-model="formData.name"
          required
          placeholder="Enter name"
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-3" label="Species:" label-for="input-3">
        <b-form-select
          id="input-3"
          v-model="formData.species"
          :options="['cats', 'dogs']"
          required
        ></b-form-select>
      </b-form-group>

      <b-form-group id="input-group-2" label="Age:" label-for="input-2">
        <b-form-input
          id="input-2"
          type="number"
          v-model="formData.age"
          required
          placeholder="Enter age"
        ></b-form-input>
      </b-form-group>

      <b-button type="submit" variant="primary">Submit</b-button>
      <b-button type="reset" variant="danger">Reset</b-button>
    </b-form>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'Home',
  data () {
    return {
      showPetForm: false,
      formData: {
        name: '',
        age: 0,
        species: null
      }
    }
  },
  computed: {
    ...mapGetters([
      'animalsCount',
      'getAllCats',
      'getAllDogs'
    ])
  },
  methods: {
    ...mapActions([
      'addPet'
    ]),
    togglePetForm () {
      this.showPetForm = !this.showPetForm
    },
    handleSubmit () {
      const { species, age, name } = this.formData
      const payLoad = {
        species,
        pet: {
          name,
          age
        }
      }
      this.addPet(payLoad)

      // reset the form after submit
      this.formData = {
        name: '',
        age: 0,
        species: null
      }
    }
  }
}
</script>
